<template>
  <div>
    <el-header class="header_bg">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item>{{asideNavTitle.length!=0?asideNavTitle[0]:""}}</el-breadcrumb-item>
        <el-breadcrumb-item>{{asideNavTitle[1].length!=0?asideNavTitle[1]:""}}</el-breadcrumb-item>
      </el-breadcrumb>
    </el-header>
    <div class="form_bg white_bg_color margin_top15">
      <el-row :class="{modifyClass:whether}">
        <el-col :span="17" class="firm_info">
          <el-row>
            <el-col :span="4">
              <p>企业代码</p>
            </el-col>
            <el-col :span="14" class="text_align_left">
              <p>{{code}}</p>
            </el-col>
            <el-col :span="6">
              <p v-if="code.length !==0">
                <img src="../../assets/firm/sccg_icon_login@2x.png" height="16" width="16"/>
                <span>已设置</span>
              </p>
              <p v-else="code.length ===0">
                <img src="../../assets/firm/weishezhi_icon@2x.png" height="16" width="16"/>
                <span>未设置</span>
              </p>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="4">
              <p>企业名称</p>
            </el-col>
            <el-col :span="14" class="text_align_left">
              <p>{{name}}</p>
            </el-col>
            <el-col :span="6">
              <p v-if="name.length !==0">
                <img src="../../assets/firm/sccg_icon_login@2x.png" height="16" width="16"/>
                <span>已设置</span>
              </p>
              <p v-else="name.length ===0">
                <img src="../../assets/firm/weishezhi_icon@2x.png" height="16" width="16"/>
                <span>未设置</span>
              </p>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="4">
              <p>联系人</p>
            </el-col>
            <el-col :span="14" class="text_align_left">
              <p>{{contacts}}</p>
            </el-col>
            <el-col :span="6">
              <p v-if="name.length !==0">
                <img src="../../assets/firm/sccg_icon_login@2x.png" height="16" width="16"/>
                <span>已设置</span>
              </p>
              <p v-else="name.length ===0">
                <img src="../../assets/firm/weishezhi_icon@2x.png" height="16" width="16"/>
                <span>未设置</span>
              </p>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="4">
              <p>联系电话</p>
            </el-col>
            <el-col :span="14" class="text_align_left">
              <p>{{contactTel}}</p>
            </el-col>
            <el-col :span="6">
              <p v-if="contactTel.length !==0">
                <img src="../../assets/firm/sccg_icon_login@2x.png" height="16" width="16"/>
                <span>已设置</span>
              </p>
              <p v-else="contactTel.length ===0">
                <img src="../../assets/firm/weishezhi_icon@2x.png" height="16" width="16"/>
                <span>未设置</span>
              </p>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="4">
              <p>联系地址</p>
            </el-col>
            <el-col :span="14" class="text_align_left">
              <p>{{address}}</p>
            </el-col>
            <el-col :span="6">
              <p v-if="address.length !==0">
                <img src="../../assets/firm/sccg_icon_login@2x.png" height="16" width="16"/>
                <span>已设置</span>
              </p>
              <p v-else="address.length ===0">
                <img src="../../assets/firm/weishezhi_icon@2x.png" height="16" width="16"/>
                <span>未设置</span>
              </p>
            </el-col>
          </el-row>
        </el-col>
        <el-col :span="7">
          <el-button class="save_btn" @click="modifyFirmInfo()">修改</el-button>
        </el-col>
      </el-row>
      <el-row :class="{modifyClass:!whether}">
        <el-col :span="14">
          <el-form :model="firmForm" label-width="80px" class="data_form" :rules="rules" ref="firmForm">
            <el-form-item label="企业代码">
              <el-input v-model="firmForm.code" autocomplete="off" placeholder="请输入企业代码"></el-input>
            </el-form-item>
            <el-form-item label="企业名称" prop="name">
              <el-input v-model="firmForm.name" autocomplete="off" placeholder="请输入企业名称" disabled=""></el-input>
            </el-form-item>
            <el-form-item label="联系人" prop="contacts">
              <el-input v-model="firmForm.contacts" autocomplete="off" placeholder="请输入真实姓名"></el-input>
            </el-form-item>
            <el-form-item label="联系电话">
              <el-input v-model="firmForm.contactTel" autocomplete="off" placeholder="请输入联系电话"></el-input>
            </el-form-item>
            <el-form-item label="联系地址">
              <el-row :gutter="10">
                <el-col :span="7">
                  <el-select v-model="firmForm.province" placeholder="省份">
                    <el-option
                      v-for="item in cities"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                      <span style="float: left">{{ item.label }}</span>
                    </el-option>
                  </el-select>
                </el-col>
                <el-col :span="7">
                  <el-select v-model="firmForm.city" placeholder="市">
                    <el-option
                      v-for="item in cities"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                      <span style="float: left">{{ item.label }}</span>
                    </el-option>
                  </el-select>
                </el-col>
                <el-col :span="7">
                  <el-select v-model="firmForm.region" placeholder="区县">
                    <el-option
                      v-for="item in cities"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                      <span style="float: left">{{ item.label }}</span>
                    </el-option>
                  </el-select>
                </el-col>
              </el-row>

            </el-form-item>
            <el-form-item label="详细地址">
              <el-input v-model="firmForm.address" autocomplete="off" placeholder="请输入详细地址"></el-input>
            </el-form-item>

          </el-form>

        </el-col>
        <el-col :span="10">
          <el-button class="save_btn margin_top30" @click="saveFirmInfo()">保存</el-button>

        </el-col>
      </el-row>
      <hr class="dashed_hr"/>
      <el-row>
        <el-col :span="3">
          <p class="number_unit"><span class="number">{{departNumber}}</span>个</p>
          <p class="number_unit">企业部门</p>
        </el-col>
        <el-col :span="3">
          <p class="number_unit"><span class="number">{{staffNumber}}</span>个</p>
          <p class="number_unit">企业成员</p>
        </el-col>
        <el-col :span="6">
          <div class="logo_div">
            上传logo
          </div>
          <p class="logo_hint">推荐尺寸180x90px，大小不超过500K</p>
        </el-col>
        <el-col :span="3">
          <div class="uploading_license">上传营业执照</div>
        </el-col>
      </el-row>
    </div>

  </div>
</template>

<script>
  import {mapGetters} from 'vuex'
  import VerifyUtils from '../../utils/utils';
  export default {
    name: "FirmData",
    props: ['selectTitle'],
    data() {
      return {
        labelPosition: 'right',
        code: '',
        name: '',
        contacts: '',
        contactTel: '',
        province: '',
        city: '',
        region: '',
        address: '',
        departNumber:'',
        staffNumber:'',
        whether: false,
        firmForm: {
          code: '',
          name: '',
          contacts: '',
          contactTel: '',
          province: '',
          city: '',
          region: '',
          address: '',
        },
        cities: [{
          value: 'Beijing',
          label: '北京'
        }, {
          value: 'Shanghai',
          label: '上海'
        }, {
          value: 'Nanjing',
          label: '南京'
        }, {
          value: 'Chengdu',
          label: '成都'
        }, {
          value: 'Shenzhen',
          label: '深圳'
        }, {
          value: 'Guangzhou',
          label: '广州'
        }],
        rules:
          {
            userName: [
              {required: true, message: '请输入真实姓名', trigger: 'blur'},
              {min: 2, max: 5, message: '长度在 2 到 5 个字符', trigger: 'blur'}
            ],
            email: [
              {required: true, message: '请输入邮箱地址', trigger: 'blur'},
              {type: 'email', message: '请输入正确的邮箱地址', trigger: ['blur', 'change']}
            ]
            // birth: [
            //   { type: 'date', required: true, message: '请选择日期', trigger: 'change' }
            // ],
          }
      }
    },
    computed: {
      ...mapGetters(['asideNavTitle'])
    },
    created() {
      this.getFirmInfo();
    },
    methods: {
      getFirmInfo(){
        let enterpriseId = localStorage.getItem("enterpriseId");
        var firmInfo = '/api/enterprise/profile/' + enterpriseId;
        this.$get(firmInfo, {
          enterpriseId: enterpriseId,
        }).then(res => {
          if (JSON.stringify(res.data.code) == 200) {
            this.code = res.data.data.identity;
            this.name = res.data.data.enterpriseName;
            this.contacts = res.data.data.contact;
            this.contactTel = res.data.data.telephone;
            this.province = res.data.data.province;
            this.city = res.data.data.city;
            this.region = res.data.data.county;
            this.address = res.data.data.address;
            this.staffNumber = res.data.data.employee;
            this.departNumber = res.data.data.department;
            console.log("test" + JSON.stringify(res.data.data));
          } else {
            alert(JSON.stringify(res.data.msg));
          }
        }).catch(function (reason) {
          console.log('catch:', reason);
        });
      },
      modifyFirmInfo() {
        this.whether = true;
        this.firmForm.code = this.code;
        this.firmForm.name = this.name;
        this.firmForm.contacts = this.contacts;
        this.firmForm.contactTel = this.contactTel;
        this.firmForm.province = this.province;
        this.firmForm.city = this.city;
        this.firmForm.region = this.region;
        this.firmForm.address = this.address;

      },
      saveFirmInfo() {
        let enterpriseId = localStorage.getItem("enterpriseId");
        var firmInfo = '/api/enterprise/profile/' + enterpriseId;
        let identity = '';
        let contact = '';
        let province = '';
        let city='';
        let county = '';
        let address = '';
        let phoneNumber = '';
        if (VerifyUtils.validateIsNull(this.firmForm.code)) {
          identity = this.firmForm.code;
        }
        if (VerifyUtils.validateIsNull(this.firmForm.code)) {
          contact = this.firmForm.contacts;
        }
        if (VerifyUtils.validateIsNull(this.firmForm.code)) {
          province = this.firmForm.province;
        }
        if (VerifyUtils.validateIsNull(this.firmForm.code)) {
          city = this.firmForm.city;
        }
        if (VerifyUtils.validateIsNull(this.firmForm.code)) {
          county = this.firmForm.region;
        }
        if (VerifyUtils.validateIsNull(this.firmForm.code)) {
          address = this.firmForm.address;
        }
        if (VerifyUtils.validateIsNull(this.firmForm.code)) {
          phoneNumber = this.firmForm.contactTel;
        }
        this.$put(firmInfo, {
          enterpriseId: enterpriseId,
          identity:identity,
          contact:contact,
          phoneNumber:phoneNumber,
          province:province,
          city:city,
          county:county,
          address:address
        }).then(res => {
          debugger
          if (JSON.stringify(res.data.code) == 200) {
            this.whether = false;
            this.getFirmInfo();
            console.log("test" + JSON.stringify(res.data.data));
          } else {
            alert(JSON.stringify(res.data.msg));
          }
        }).catch(function (reason) {
          console.log('catch:', reason);
        });

      }
    }

  }
</script>

<style scoped>
  @import "../../styles/personal/personal-center.scss";

  .header_bg {
    background-color: white;
    text-align: right;
    font-size: 18px;
    color: #333333;
  }

  .dashed_hr {
    height: 5px;
    border: none;
    border-top: 3px dashed #6f7180;

  }

  .modifyClass {
    display: none;
  }

  .el-breadcrumb {
    line-height: 3.5;
    font-size: 18px;
  }

  .form_bg {
    margin-top: 20px;
  }

  .data_form {
    width: 360px;
    padding-top: 30px;
    padding-bottom: 30px;
  }

  .firm_info {
    font-size: 14px;
    color: #666666;
  }

  .firm_info_content {
    font-size: 14px;
    color: #333333;
  }

  .save_btn {
    width: 180px;
    background-color: #33B2E8;
    border-radius: 4px;
    color: white;
    margin-top: 10px;
  }

  .margin_top30 {
    margin-top: 20px;
  }

  .logo_div {
    width: 180px;
    height: 90px;
    opacity: 0.7;
    background: #000000;
  }

  .uploading_license {
    width: 75px;
    height: 90px;
    opacity: 0.7;
    background: #000000;
  }

  .number {
    font-size: 30px;
    color: #33B2E8;
  }

  .number_unit {
    font-size: 14px;
    color: #404040;
  }

  .logo_hint {
    font-size: 12px;
    color: #666666;
  }
</style>



