<template>
  <div class="bg_color_gray">
    <el-row :gutter="30" class="margin_left0 white_bg_color" style="margin-left: 0px;margin-right: 0px">
      <el-col :span="5" class="user_img">
        <img src="../../../assets/personal/touxiang_bg@2x.png" height="90" width="90"/>
      </el-col>
      <el-col :span="19" class="text_align_left">
        <p class="personal_info">ID:{{userId}}</p>
        <p class="personal_info">微信：<span v-if="weChat.length !== 0">{{weChat}}</span><span v-else-if="weChat.length === 0"><span>未绑定</span>&emsp;<span class="edit_info">去绑定>></span></span></p>
        <p class="personal_info" v-model="tel">{{tel}}&emsp;&emsp;&emsp;<span class="edit_info" @click="modifyTel()" >修改>></span></p>
      </el-col>
    </el-row>
    <div class="set_data white_bg_color">

      <el-form :model="userForm" label-width="80px" class="data_form" :rules="rules" ref="userForm">
        <el-form-item label="昵称">
          <el-input v-model="userForm.nickname" autocomplete="off" placeholder="请输入昵称"></el-input>
        </el-form-item>
        <el-form-item label="性别" prop="sex">
          <!--<el-radio-group  id="sexRadios" :options="sexOptions" name="sexRadio">-->
          <!--</el-radio-group>-->
          <el-radio v-model="userForm.sex" label="1">男</el-radio>
          <el-radio v-model="userForm.sex" label="0">女</el-radio>
        </el-form-item>
        <el-form-item label="真实姓名" prop="username">
            <el-input v-model="userForm.userName" autocomplete="off" placeholder="请输入真实姓名"></el-input>
        </el-form-item>
        <el-form-item label="出生日期">
            <el-form-item>
              <!--<el-input v-if="userForm.birth.length!==0" v-model="userForm.birth" autocomplete="off"></el-input>-->
              <el-date-picker  type="date" placeholder="请选择出生日期" v-model="userForm.birth" class="width280"></el-date-picker>
            </el-form-item>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="userForm.email" autocomplete="off" placeholder="请输入邮箱"></el-input>
          <span class="hint_email">*可用于找回密码</span>
        </el-form-item>
        <el-button class="save_btn" @click="saveUserInfo()">保存</el-button>
      </el-form>

    </div>
  </div>
</template>

<script>
  import Verify from "../../../utils/utils"
  import swal from 'sweetalert2'; //alert框
  import {mapGetters} from 'vuex'

  export default {
    name: "PersonalData",
    props: ['selectTitle'],
    data() {
      return {
        userId:'',
        tel:'',
        weChat:'',
        userForm: {
          nickname: '',
          userName: '',
          sex: 1,
          birth: '',
          email: '',
        },
        sexSelected: '1',
        // sexOptions: [{text: '男', value: '1'}, {text: '女', value: '2'}],
        rules:
          {
            userName: [
              {required: true, message: '请输入真实姓名', trigger: 'blur'},
              {min: 2, max: 5, message: '长度在 2 到 5 个字符', trigger: 'blur'}
            ],
            email:[
              { required: true, message: '请输入邮箱地址', trigger: 'blur' },
              { type: 'email', message: '请输入正确的邮箱地址', trigger: ['blur', 'change']}
            ]
            // birth: [
            //   { type: 'date', required: true, message: '请选择日期', trigger: 'change' }
            // ],
          }
      }
    },
    computed: {
      ...mapGetters(['asideNavTitle'])
    },

    created(){
      this.userId = localStorage.getItem("userId");
      this.tel = localStorage.getItem("phoneNumber");
      let weChat = localStorage.getItem("weChat");
      let birthday = localStorage.getItem("birthday");
      let nickname = localStorage.getItem("nickname");
      let name = localStorage.getItem("name");
      let email = localStorage.getItem("email");
      let gender = localStorage.getItem("gender");

      if (Verify.validateIsNull(weChat)) {
        this.weChat = weChat;
      }
      if (Verify.validateIsNull(birthday)){
        this.userForm.birth = birthday;
      }
      if (Verify.validateIsNull(nickname)){
        this.userForm.nickname = nickname;
      }
      if (Verify.validateIsNull(name)){
        this.userForm.userName = name;
      }
      if (Verify.validateIsNull(email)){
        this.userForm.email = email;
      }
      if (Verify.validateIsNull(gender)){
        this.userForm.sex = gender;
      }
    },
    methods:{
      //修改手机号
      modifyTel(){
        this.$router.push({ path: '/SafeSetting'})
      },
      saveUserInfo(){
        swal({
          title: "是否保存?",
          text: "",
          type: "success",
          showCancelButton: true,
          confirmButtonColor: "#DD6B55",
          confirmButtonText: "确定",
          cancelButtonText: "取消"
        }).then((result) => {
          if (result.value) {
            let editUserInfo = "/api/user/profile/" + this.userId;
            this.$post(editUserInfo, {
              nickname: this.userForm.nickname,
              name: this.userForm.userName,
              userId: this.userId,
              birthday:this.userForm.birth,
              gender:this.userForm.sex,
              email:this.userForm.email
            }).then(res => {
              if (JSON.stringify(res.data.code) == 200) {
                swal("提示", "已保存成功", "success");
              }else {
                swal(JSON.stringify(res.data.message), "请稍后重试", "error");
              }
              // console.log("res=="+JSON.stringify(res))
            }).catch(function (reason) {
              console.log(JSON.stringify(reason));
            });
          } else {
            swal("Cancelled", "", "");
          }
        });

      }
    }
  }
</script>

<style scoped>
  @import "../../../styles/personal/personal-center.scss";

  .set_data {
    margin-top: 10px;
  }

  .user_img {
    text-align: center;
    margin-top: 20px;
  }

  .personal_info {
    font-size: 14px;
    color: #404040;
  }

  .edit_info {
    color: #33B2E8;
  }

  .data_form {
    width: 360px;
    padding-top: 30px;
    padding-bottom: 30px;
  }
  .hint_email{
    color:#666666;
    font-size: 12px;
    /*text-align: left;*/
    float: left;
  }
  .save_btn{
    width:300px;
    color: white;
    background-color: #33B2E8;
    font-size: 17px;
  }
  .width280{
    width: 280px;
  }
</style>



