<template>    
  <div class="all_category">
    <div class="category">
      <div class="div_bottom margin_top5">
        <el-row>
          <el-col :span="2" class="statistics_font">
            <div class="btn_bg_color">所在地区</div>
          </el-col>
          <el-col :span="22" class="regular_font">
            <div>
              <ul class="ul_list_style">
                <li v-for="item in areaType" class="padding_left10 padding_right10">
                  <a v-text="item.name" v-on:click="selectType(item.id,item.name)"
                     v-bind:class="{active : selectTypeName === item.name }"></a>
                </li>
              </ul>
            </div>
          </el-col>
        </el-row>
      </div>
      <div class="div_bottom">
        <el-row>
          <el-col :span="2" class="statistics_font">
            <div class="btn_bg_color">机构类型</div>
          </el-col>
          <el-col :span="22" class="regular_font">
            <div>
              <ul class="ul_list_style">
                <li v-for="project in institutionType" class="padding_left10 padding_right10">
                  <a v-text="project.proName" v-on:click="selectType(project.id,project.proName)"
                     v-bind:class="{active : selectTypeName === project.proName }"></a>
                </li>
              </ul>
            </div>
          </el-col>
        </el-row>
      </div>
    </div>
    <div class="suit_list">
      <el-row class="regular_font">
        <el-col :span="2">
          <div class="suit_btn">综合</div>
        </el-col>
        <el-col :span="2">
          <div>距离</div>
        </el-col>
        <el-col :span="20" class="text_align_right">
          <div class="statistics_font">共<span class="eligible_number">{{eligibleNumber}}</span>个符合条件的套餐</div>
        </el-col>
      </el-row>
    </div>
    <div>
      <el-row :gutter="40">
        <el-col :span="18">
          <el-row :gutter="40">
            <el-col :span="8" v-for="(o, index) in 12" :key="o" :class="{'marginTop10':index>2}">
              <el-card :body-style="{ padding: '0px' }">
                <img src="../../assets/checkUp/taocan_img1@2x.png" class="image">
                <div class="card_content card_align">
                  <p class="card_title">慈铭体检中心</p>
                  <span class="card_subtitle">共{{sumSuit}}个体检套餐</span>
                  <p class="margin_top0_bottom0"><span class="discount_price">¥</span><span
                    class="discount_price">558</span><span class="origin_price">起</span>&emsp;&emsp;&emsp;<span
                    class="origin_price line">¥</span><span class="origin_price line">892</span></p>
                </div>
              </el-card>
            </el-col>
          </el-row>
        </el-col>
        <el-col :span="6">
          <div class="float_left regular_font">
            推荐套餐
          </div>
          <el-row>
            <el-col :span="20" v-for="(o, index) in 4" :key="o">
              <el-card :body-style="{ padding: '0px' }">
                <img src="../../assets/checkUp/taocan_img1@2x.png" class="image">
                <div class="card_content card_align">
                  <p class="card_title">入职体检套餐</p>
                  <!--<span class="card_subtitle">颈腰椎劳损、肿瘤筛查</span>-->
                  <p class="margin_top0_bottom0"><span class="discount_price">¥</span><span
                    class="discount_price">558</span><span class="origin_price">起</span>&emsp;&emsp;&emsp;<span
                    class="origin_price line">¥</span><span class="origin_price line">892</span></p>
                </div>
              </el-card>
            </el-col>
          </el-row>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        period: '',
        selectTypeName: '不限',
        areaType: [{'id': '1', 'name': '东城区'}, {'id': '2', 'name': '西城区'}, {'id': '3', 'name': '朝阳区'}, {
          'id': '4',
          'name': '丰台区'
        }, {'id': '5', 'name': '石景山区'}],
        institutionType: [{'id': '1', 'proName': '公里医院'}, {'id': '2', 'proName': '民营医院'}, {
          'id': '3',
          'proName': '专业体检中心'
        }],
        eligibleNumber: 256,
        sumSuit:126
      }
    },
    methods: {
      selectType(params) {
        console.log("params===" + params);
      }
    }
  }
</script>
<style>
  @import "../../styles/personal/personal-center.scss";

  .all_category {
    padding-left: 10%;
    padding-right: 10%;
    /*margin-top: 15px;*/
    line-height: 38px;
  }

  .category {
    border: 1px solid #EEEEEE;
    background-color: #ffffff;
  }

  .regular_font {
    color: #333333;
    font-size: 14px;
  }

  .btn_bg_color {
    background-color: #f8f8f8;
    height: 38px;
  }

  div.category ul {
    list-style: none; /* 去掉ul前面的符号 */
    margin: 0px; /* 与外界元素的距离为0 */
    padding: 0px; /* 与内部元素的距离为0 */
    width: auto; /* 宽度根据元素内容调整 */
  }

  /* 所有class为menu的div中的ul中的li样式 */
  div.category ul li {
    float: left; /* 向左漂移，将竖排变为横排 */
  }

  .div_bottom {
    border-bottom: 1px solid #EEEEEE;
  }

  .margin_top5 {
    margin-top: 5px;
  }

  /*筛选条件的样式*/
  .suit_list {
    background-color: white;
    margin-top: 10px;
    margin-bottom: 10px;
    height: 38px;
  }

  .suit_btn {
    height: 38px;
  }

  .eligible_number {
    color: #33B2E8;
    font-size: 14px;
  }

  .statistics_font {
    color: #666666;
    font-size: 14px;
  }
  .marginTop10 {
    margin-top: 20px;
  }
  .card_align{
    text-align: left;
    padding-left: 20px;
    line-height: 30px;
  }

</style>



