<template>
  <div>
    <el-header class="header_bg">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item>{{asideNavTitle[0]}}</el-breadcrumb-item>
        <el-breadcrumb-item>{{asideNavTitle[1]}}</el-breadcrumb-item>
      </el-breadcrumb>
    </el-header>
    <div class="white_bg_color margin_top15">
      <p class="p_title text_align_left padding_left10 padding_top10">订单列表</p>
      <el-row>
        <el-col :span="6">
          <el-row>
            <el-col :span="8">
              <p class="order_font">订单类型</p>
            </el-col>
            <el-col :span="16">
              <el-select v-model="formInline.region" placeholder="所有类型" style="width: 120px">
                <el-option
                  v-for="item in cities"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                  <span style="float: left">{{ item.label }}</span>
                </el-option>
              </el-select>
            </el-col>
          </el-row>
        </el-col>
        <el-col :span="6">
          <el-row>
            <el-col :span="8">
              <p class="order_font">状态</p>
            </el-col>
            <el-col :span="16">
              <el-select v-model="formInline.region" placeholder="所有类型" style="width: 120px">
                <el-option
                  v-for="item in cities"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                  <span style="float: left">{{ item.label }}</span>
                </el-option>
              </el-select>
            </el-col>
          </el-row>
        </el-col>
        <el-col :span="10">
          <el-row>
            <el-col :span="4"><p class="order_font">日期</p></el-col>
            <el-col :span="9">
              <el-date-picker type="date" placeholder="选择日期" v-model="formInline.date1"
                              style="width: 100%;"></el-date-picker>
            </el-col>
            <el-col :span="2"><p class="order_font">至</p></el-col>
            <el-col :span="9">
              <el-date-picker type="date" placeholder="选择日期" v-model="formInline.date1"
                              style="width: 100%;"></el-date-picker>
            </el-col>
          </el-row>
        </el-col>
        <el-col :span="2">
          <el-button type="primary" @click="onSubmit">搜索</el-button>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <p class="p_title text_align_left padding_left10">作废订单</p>
        </el-col>
        <el-col :span="12">
          <p class="export_font">导出</p>
        </el-col>
      </el-row>
      <div class="padding_left10 padding_right10">
        <el-table
          ref="multipleTable"
          :data="tableData3"
          tooltip-effect="dark"
          style="width: 100%"
          @selection-change="handleSelectionChange" :header-cell-style="tableHeaderColor">
          <el-table-column
            type="selection"
            width="55">
          </el-table-column>
          <el-table-column
            prop="number"
            label="订单号">
          </el-table-column>
          <el-table-column
            prop="name"
            label="产品">
          </el-table-column>
          <el-table-column
            prop="order"
            label="订单金额">
          </el-table-column>
          <el-table-column
            prop="order"
            label="状态">
            <template slot-scope="scope">
              <span class="special_color">{{scope.row.order}}</span>
            </template>
          </el-table-column>
          <el-table-column
            prop="date"
            label="创建时间">
          </el-table-column>
          <el-table-column
            prop="date"
            label="支付时间">
          </el-table-column>
          <el-table-column
            fixed="right"
            label="操作"
            width="200">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="detailInfo(scope.row)">详细</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
    <div class="white_bg_color margin_top15" style="width: 100%">

      <div class="block" v-if="total>0">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="10"
          layout="total, prev, pager, next"
          :total=total>
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
  import {mapGetters} from 'vuex'
  export default {
    name: "OrderManage",
    data() {
      return {
        tableData3: [{
          number:'12342344',
          date: '2016-05-03',
          name: '王小虎',
          order:'122222'
        }],
        currentPage: 1,
        total: 0,
        formInline: {
          user: '',
          region: '',
          date1: '',
          date2: ''
        },
        cities: [{
          value: 'Beijing',
          label: '北京'
        }, {
          value: 'Shanghai',
          label: '上海'
        }, {
          value: 'Nanjing',
          label: '南京'
        }, {
          value: 'Chengdu',
          label: '成都'
        }, {
          value: 'Shenzhen',
          label: '深圳'
        }, {
          value: 'Guangzhou',
          label: '广州'
        }],
        multipleSelection: [],
      }
    },
    computed: {
      ...mapGetters(['asideNavTitle'])
    },
    methods: {
      onSubmit() {
        console.log('submit!');
      },
      detailInfo() {

      },
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
        this.currentPage = val;
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      // 修改table header的背景色
      tableHeaderColor({row, column, rowIndex, columnIndex}) {
        if (rowIndex === 0) {
          return 'background-color: #F6F6F6;color: #404040;font-weight: 500;'
        }
      }
    }
  }
</script>

<style scoped>
  @import "../../../styles/personal/personal-center.scss";

  .header_bg {
    background-color: white;
    text-align: right;
    font-size: 18px;
    color: #333333;
  }

  .el-breadcrumb {
    font-size: 18px;
    line-height: 3.5;
    color: #333333;
  }

  .p_title {
    font-size: 14px;
    color: #404040;
    /*padding-top: 20px;*/
  }

  .export_font {
    color: #009BDD;
    font-size: 14px;
    text-align: right;
    padding-right: 10px;
  }
  .order_font{
    margin-top: 10px;
    margin-bottom: 10px;
    color: #666666;
    font-size: 14px;
  }
</style>



