<template>
  <div>
    <el-header class="header_bg">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item>{{asideNavTitle.length!=0?asideNavTitle[0]:""}}</el-breadcrumb-item>
        <el-breadcrumb-item>{{asideNavTitle.length!=0?asideNavTitle[1]:""}}</el-breadcrumb-item>
      </el-breadcrumb>
    </el-header>
    <div>
      <el-table
        :data="tableData"
        class="account_table">
        <el-table-column
          prop="date"
          label="时间"
          width="180">
        </el-table-column>
        <el-table-column
          prop="name"
          label="存入"
          width="180">
        </el-table-column>
        <el-table-column
          prop="address"
          label="支付">
        </el-table-column>
        <el-table-column
          prop="address"
          label="备注">
        </el-table-column>
      </el-table>
      <el-pagination
        background
        layout="prev, pager, next"
        :total="1000">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import {mapGetters} from 'vuex'

  export default {
    name: "AccountInfo",
    props: ['selectTitle'],
    data() {
      return {
        tableData:[]
      }
    },
    computed: {
      ...mapGetters(['asideNavTitle'])
    },
    created() {
      // alert('selectTitle==='+this.selectTitle);
      console.log('selectTitle', this.selectTitle)
    },
  }
</script>

<style scoped>
  .header_bg {
    background-color: white;
    text-align: right;
    font-size: 18px;
    color: #333333;
  }
  .el-breadcrumb {
    line-height: 3.5;
    font-size: 18px;
  }
  .account_table {
    margin-top: 15px;
    width: 100%;
    height: 360px;
  }
  .el-pagination {
    background-color: white;
  }
  .el-pagination.is-background .el-pager li:not(.disabled).hover{
    background-color: #33B2E8;
  }
  .el-pagination.is-background .el-pager li:not(.disabled).active{
    background-color: #33B2E8;
  }
</style>



