<template>
  <div>
    <div class="bg_color_gray">
      <el-row :gutter="30" class="margin_left0" style="margin-left: 0px">
        <el-col :span="15" class="white_bg_color">
          <el-row :gutter="10" class="text_align_left">
            <el-col :span="5" class="user_img">
              <img src="../../assets/personal/touxiang_bg@2x.png" height="90" width="90"/>
            </el-col>
            <el-col :span="19">
              <p class="personal_info">您好，{{userName}}</p>
              <p class="personal_info">这是您加入爱惠康第<span class="day_font">{{joinDay}}</span>天</p>
              <p class="personal_info">您当前可享受<span class="day_font">{{discount}}</span>折优惠</p>
              <p class="change_personal_info"></p>
              <el-row class="text_align_left">
                <el-col :span="12">
                  <p class="margin_top0_bottom0"><span class="invite_number">{{accountBalance}}</span><span class="ren">人</span></p>
                  <span class="personal_info">已邀请</span>
                </el-col>
                <el-col :span="12">
                  <p class="margin_top0_bottom0"><span class="invite_number">{{coupons}}</span><span class="ren">人</span></p>
                  <span class="personal_info">剩余可邀请</span>
                </el-col>
              </el-row>
            </el-col>
          </el-row>
        </el-col>
        <el-col :span="9" class="">
          <el-row class="">
            <el-col :span="24">
              <img src="../../assets/personal/wdjt_banner@2x.png" height="179" width=""/></el-col>
          </el-row>
        </el-col>
      </el-row>
      <div class="margin_top15 margin_bottom15">
        <div class="coupon_div">
          <el-row :gutter="50" style="margin-left: 0px;margin-right: 0px">
            <el-col :span="6" v-for="(o, index) in 9" :key="o" :class="{'marginTop10':index>3}" >
              <div >
                <el-row :gutter="10" class="text_align_left family_member">
                  <div >
                    <el-col :span="10" class="user_img">
                      <img src="../../assets/personal/touxiang_bg@2x.png" height="45" width="45"/>
                    </el-col>
                    <el-col :span="14">
                      <p class="personal_info">{{userName}}</p>
                      <p class="change_personal_info"><u>赠送体检</u></p>
                    </el-col>
                  </div>
                </el-row>
              </div>
            </el-col>
            <!--<el-col :span="2">-->
              <!--&emsp;-->
            <!--</el-col>-->
          </el-row>
        </div>
        <!--<compent :is="modelCode"></compent>-->
      </div>
    </div>
  </div>

</template>

<script>
  import {mapGetters} from 'vuex'
  export default {
    name:'MyFamily',
    props: ['selectTitle'],
    data() {
      return {
        userName:'test',
        joinDay:365,
        discount:'85',
        tel: '156****1234',
        activeIndex: '1',
        accountBalance:0.00,
        coupons:0
      }
    },
    computed: {
      ...mapGetters(['asideNavTitle'])
    },

    created(){
      this.getPersonInfo();
      this.getPersonBalance();
    },
    methods: {
      getPersonInfo(){
        // this.userName = localStorage.getItem("nickname");
      },
      getPersonBalance(){
        let userId = localStorage.getItem("userId");
        let userInfo = '/api/user/account/'+ userId;
        this.$get(userInfo, {
          userId: userId,
        }).then(res => {
          if (JSON.stringify(res.data.code) == 200) {
            this.accountBalance = res.data.data.balance;
            // console.log("test" + JSON.stringify(res.data.data));
          } else {
            alert(JSON.stringify(res.data.msg));
          }
        }).catch(function (reason) {
          console.log('catch:', reason);
        });
      },
      handleSelect(key, keyPath) {
        console.log(key, keyPath);
      }
    }
  }
</script>

<style>
  @import "../../styles/personal/personal-center.scss";

  .margin_left0 {
    margin-left: 0px;
  }

  /*个人信息*/
  .text_align_left {
    text-align: left;
  }

  .user_img {
    text-align: center;
    margin-top: 20px;
  }

  .personal_info {
    color: #404040;
    font-size: 14px;
  }

  .change_tel {
    color: #33B2E8;
    font-size: 14px;
  }

  .change_personal_info {
    /*float: right;*/
    color: #33B2E8;
    font-size: 14px;
  }

  /*邀请数字*/
  .invite_number {
    color: #33B2E8;
    font-size: 30px;
  }

  .ren {
    font-size: 14px;
    color: #666666;
  }
  .family_member{
    border: 1px solid #CCCCCC;
    margin-top: 15px;
  }
  .marginTop10 {
    margin-top: 10px;
    margin-bottom: 10px;
  }
  .coupon_div{
    height: auto;
    background-color: white;
  }
  .day_font{
    color: #DF2321;
    font-size: 14px;

  }
</style>






