<template>
  <el-container>
    <div class="banner">
      <el-row :gutter="10">
        <el-col :span="4" class="text_align_right margin_top5">
          <span>神州四达旗下品牌</span>
        </el-col>
        <el-col :span="8">
          <div class="text_align_left margin_top5">
            <span>北京</span>
            <span>|</span>
            <el-dropdown>
          <span class="el-dropdown-link">
            更多城市<i class="el-icon-arrow-down el-icon--right"></i>
          </span>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item>上海</el-dropdown-item>
                <el-dropdown-item>深圳</el-dropdown-item>
                <el-dropdown-item>广东</el-dropdown-item>
                <el-dropdown-item>山东</el-dropdown-item>
                <el-dropdown-item>东北</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="text_align_right margin_top5">
            <span>您好，欢迎您</span>
            <span v-if="loginStatus !=1">
              <router-link :to="{path:'/'}" active-class="active" class="login_btn">
                登录
              </router-link>
              <span>或</span>
              <router-link :to="{path:'/'}" active-class="active" class="login_btn">
                免费注册
              </router-link>
            </span>
            <span v-else="loginStatus==1">
              <router-link :to="{path:'/Login'}" active-class="active" class="login_btn">
                退出登录
              </router-link>
            </span>

          </div>
        </el-col>
        <el-col :span="2">
          <div class="text_align_right margin_top5">
            我的订单
          </div>
        </el-col>
        <el-col :span="1">
          <div class="margin_top5">
            购物车
          </div>
        </el-col>
        <el-col :span="2">
          <div class="text_align_left margin_top5">
            <router-link v-if="enterpriseId === 'undefined' " :to="{path:'PersonalCenter',query: {name: 'person'}}" active-class="active" class="personal_center" @click="judgeParam('person')">
              个人中心
            </router-link>
            <router-link v-if="userId === 'undefined'" :to="{path:'/FirmCenter',query: {name: 'firm'}}" active-class="active" class="personal_center" @click="judgeParam('firm')">
              企业中心
            </router-link>
          </div>
        </el-col>
      </el-row>
    </div>
  </el-container>
</template>

<script>
  export default {
    name: "Banner",
    data(){
      return{
        loginStatus:'0',
        userId:'',
        enterpriseId:''
      }
    },
    created(){
     this.loginStatus = localStorage.getItem("loginStatus");
     this.enterpriseId = localStorage.getItem("enterpriseId");
     this.userId = localStorage.getItem("userId");
     console.log(' this.loginStatus=='+ this.loginStatus + "enterpriseId=="+this.enterpriseId +"userId=="+this.userId)
    },
    methods:{
      judgeParam(para){
        localStorage.setItem('center',para);
        console.log('center=='+para);
      }
    }
  }
</script>

<style scoped>
  .banner {
    width: 100%;
    height: 2rem;
    margin-top: 0px;
    background-color: rgba(246, 246, 246, 1);
    font-size: 0.81rem;
    border-bottom: 1px solid #f2f2f2;

  }

  .banner span,div {
    color: #6C6C6C;

  }
  .el-dropdown{
    font-size: 0.81rem;
  }
  .margin_top5 {
    margin-top: 5px;
  }

  .text_align_left {
    text-align: left;
  }

  .text_align_right {
    text-align: right;
  }
  .login_btn{
    color: #33B2E8;
  }
  .personal_center{
    text-decoration:none;
  }
</style>



